* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country__name {
  display: flex;
  align-items: center;
}
.cantry__item {
  list-style: none;
  text-align: start;
  margin-bottom: 10px;
}
.country__title {
  margin-left: 15px;
  margin: 0;
  margin-left: 15px;
  display: inline-block;
}
.cantry__text {
  font-size: 23px;
  font-weight: 500;
  margin: 0;
}

.cantry__span {
  font-size: 23px;
  font-weight: 700;
}

.country-list {
  padding: 0;
  margin: 0;
  margin-top: 30px;
}
.cantry__item__two {
  display: flex;
  align-items: center;
}
